	module.exports = {"data":{"markdownRemark":{"html":"<p>Players form two lines of equal length. Mix it up so players are not across from someone they've done senes with already.</p>\n<ol>\n<li>The person from the right line will initiate each scene with a single line.</li>\n<li>The left person will respond with a single line.</li>\n<li>The right person responds with a single line.</li>\n</ol>\n<p>Goal is to answer the questions \"Who?\" \"What?\" \"Where?\" in only three lines.\nDoing this will establish the scene for the players and the audience up top so the rest of the scene is less work.\nPlayers switch lines when he/she has completed a scene. Continue until every player has completed a scene from both lines.</p>","frontmatter":{"title":"Who, What, and Where in Three Lines","type":"Exercise","foci":["Content"],"minimumPeople":2,"duration":2,"durationType":"linear","peoplePerScene":null}}},"pathContext":{"slug":"/who-what-where-in-three-lines/"}}

